:root {
    --primary-color: #6b84df;
    --primary-color-hover:  #5976dd;
    --primary-accent-color: #2c2f33;
    --primary-text-color: #e4e5e6;
    --secondary-text-color: #99aab5;
    --background-color: #36393f;
    --secondary-background-color: #2f3136;
    --tertiary-background-color: #202225;
}

body {
    background-color: var(--secondary-background-color);
    color: var(--primary-text-color);
}

.stars-div {
    flex-direction: column;
    place-items: center;
}

.stars {
    flex-direction: row;
    font-size: 100px;
    margin-bottom: 30px;
}

.rating-phrase {
    font-size: 40px;
}

.open-dialog-button {
    margin-top: 15px;
}

.open-dialog-button, .close-dialog-button {
    padding: 15px;
    border-radius: 50px;
    background-color: var(--primary-color);
    color: var(--primary-text-color);
    transition: 0.2s all ease-in-out;
}

.open-dialog-button:hover, .close-dialog-button:hover {
    background-color: var(--primary-color-hover);
    transition: 0.2s all ease-in-out;
}

.dialog-div {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    align-items: center;
    justify-content: center;
    height: 300px;
    background-color: var(--background-color);
    padding: 20px;
    border-radius: 8px;
}
  
.dialog-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin: 5px;
}

.dialog-text {
    font-size: 40px;
}